import React, { useContext, useState, useEffect } from 'react';
import './PortfolioItem.component.scss';
import ScrollDownIndicator from '../../components/ScrollDownIndicator/ScrollDownIndicator.component.jsx';
import Markdown from 'markdown-to-jsx';
import SectionH2 from '../../components/SectionH2/Sectionh2.component.jsx';
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { AppContext } from '../../AppContext';
import { motion, AnimatePresence } from 'framer-motion';

export default function PortfolioItem(props) {
  let location = useLocation();
  const pathname = location.pathname;
  const { portfolioData, isMD } = useContext(AppContext);
  const [textLink, setTextLink] = useState('');
  const [text, setText] = useState('');

  useEffect(() => {

    portfolioData.map( data => `/${data.link}` === pathname && setTextLink(data.markdown) );

    import(`../../data/markdown/${textLink}`)
      .then((res) => {
        fetch(res.default)
          .then((res) => res.text())
          .then((res) => setText(res))
          .catch((err) => console.log(err));
      })
      .catch((err) => console.log(err));

  }, [pathname, textLink, portfolioData]);

  const navbar = document.getElementById('navbar');
  const navbarHeight = navbar.offsetHeight;
  
  return (
    <>
      {
        portfolioData.map( data => (
          `/${data.link}` === pathname &&
          <>
            <Helmet>
              <title>{data.title}</title>
              <meta
                name="description"
                content={`ISR Technology - USA, MIAMI, FL. ${data.paragraph}`}
              />
            </Helmet>
            <AnimatePresence>
              <div className="service-image-socket full-vh" id="service-image-socket">
                <div className="overlay"></div>
                <motion.img
                  src={process.env.REACT_APP_PUBLIC_URL + data.image}
                  alt={data.title}
                  className="portfolio-image"
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  exit={{ opacity: 0 }}
                  transition={{
                    opacity: { duration: 1 },
                  }}
                />
                <div className="service-text">
                  <motion.h1 
                    initial={{ opacity: 0 }}  
                    animate={{ opacity: 1 }} 
                    transition={{ delay: 0.5, duration: 0.5, ease: 'easeIn'}} 
                    className={isMD && `display2`}>
                      {data.title}
                  </motion.h1>
                </div>
                <ScrollDownIndicator />
              </div>
            </AnimatePresence>
            <div className="service-page-content grid" id="service-page-content" style={{paddingBottom: navbarHeight}}>
              <div className="page-header" id="page-header" style={{height: navbarHeight}}></div>
              <div className="service-page-text">
                <div className="service-page-header">
                  <SectionH2 h2={data.title} />
                  <div className="underline"></div>
                </div>
                <div className="service-page-body">
                  <Markdown>{text}</Markdown>
                </div>
              </div>
            </div>
          </>
        )
      )}
    </>
  );
}